import {Component, OnInit} from '@angular/core';
import {Person} from '../person';
import {StudentsService} from '../service/students.service';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {DeleteComponent} from '../delete/delete.component';
import {Student} from '../ultis/student';
import {EditComponent} from '../edit/edit.component';
import {AddComponent} from '../add/add.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  lst: any;

  option: any = {
    size: 'lg',
    centered: true
  };

  constructor(
    private service: StudentsService,
    private modal: NgbModal,
  ) {
  }

  ngOnInit(): void {
    // this.intData();
    this.getList();
  }

  intData() {
    this.lst = [
      {id: 1, first: 'Mark', last: 'Otto', handle: '@mdo'},
      {id: 2, first: 'Jacob', last: 'Thornton', handle: '@fat'},
      {id: 3, first: 'Larry', last: 'the Bird', handle: '@twitter'},
    ];
  }

  getList() {
    this.service.getList().subscribe(res => {
      console.log(res.data);
      this.lst = res.data;
    });
  }

  deleteStudent(item: Student) {
    const modalRef = this.modal.open(DeleteComponent, this.option);
    modalRef.componentInstance.name = item.fullName;
    modalRef.componentInstance.id = item.id;
    modalRef.result.then(res => {
      this.getList();
    });

  }

  editStudent(item: Student) {
    const modalRef = this.modal.open(EditComponent, this.option);
    modalRef.componentInstance.student = item;
    modalRef.result.then(res => {
      this.getList();
    });
  }

  addStudent() {
    const modalRef = this.modal.open(AddComponent,this.option);
    modalRef.result.then(res => {
      this.getList();
    })
  }
}
